@use "global" as *;
.button {
  display: inline-block;
  text-align: center;
  width: 100%;
  max-width: rem(335);
  padding: rem(9) 0;
  color: $white;
  border: 1px solid $white;
  font-size: rem(20);
  letter-spacing: rem(2);
  overflow: hidden;
  position: relative;

  @include mq("md") {
    max-width: rem(360);
    padding: rem(17) 0;
    font-size: rem(16);
  }
}

.button::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $black;
  z-index: -2;
}

.button::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $white;
  z-index: -1;
  transform: translateX(-100%);
  transition: 0.3s ease-in-out;
}

.button:hover {
  color: $black;
}

.button:hover::before {
  transform: translateX(0);
}
